import { observer } from 'mobx-react-lite'
import styles from './communitymember.module.scss'
import { getToken } from '@/utils/token'
import { useEffect } from 'react'
import { useStore } from '@/store'
import { useLocation, useNavigate, useParams } from 'react-router-dom'
import { NavBar } from 'antd-mobile'
import { Button , Dialog } from 'antd-mobile'
import Icon from '@/components/Icon/icon'
import { NavLink } from 'react-router-dom'


const CommunityMember = () => {
    
    const { communityid } = useParams()
    const userphone = getToken().userphone
    const { communityStore,userStore,loginStore } = useStore()
    const communityInfoParams:any  = useLocation().state 
    const navigate = useNavigate()

    useEffect(() =>{
    // action
        const runAction = async () => {
            await communityStore.getMycommunityNftPASS(Number(communityid),userphone)
        }
        runAction()
    }, [communityStore,communityid,userphone])

    // console.log("+++++++++++++++++++++++   ",communityInfoParams)


    useEffect(() =>{
    // action
        const runAction = async () => {
            await communityStore.getMycommunityMedal(Number(communityid),userphone)
        }
        runAction()
    }, [communityStore,communityid,userphone])


    useEffect(() =>{
    // action
        const runAction = async () => {
            await communityStore.getMycommunityMedal(Number(communityid),userphone)
        }
        runAction()
    }, [communityStore,communityid,userphone])
    
    useEffect(() => {
        const runAction = async () => {
        await userStore.getMyUserDisplay(userphone)
        }
        runAction()
    }, [userStore, userphone])

    function loginOut() {
        Dialog.show({
          title: '',
          content: '',
          closeOnAction: true,
          actions: [
            [
              {
                key: 'cancel',
                text: ''
              }, {
                key: 'confirm',
                text: '',
                danger: true,
                onClick: () => {
                  // 1.token
                  // 2.
                  loginStore.loginOut()
                  navigate(`/${communityInfoParams.communityname}`)
                }
                
              }
            ]
          ]
        })
      }
      const myholdnft = () => {
        navigate(`/myholdnft/${communityid}`)
    }

    const communityDetail=()=>{
        navigate(`/island/${communityInfoParams.communityname}`)
    }


    // const myholdnft = () => {
    //     navigate(`/{myholdnft}/${communityid}`)
    // }
    return(
        <div className={styles.root}>
            <div className='bgcircle1'></div>
            <div className='bgcircle2'></div>
            <div className='bgcircle3'></div>
            <div className='bgcircle4'></div>
            <NavBar className='navbar' onBack={()=>window.history.back()}></NavBar>
            <div className='note'>
            <Icon type="logout"  onClick={loginOut}/>
            
            </div>
            <div className='title'></div>
            <div className='note'>:{userStore.myuserInfo.userwalletaddress}</div>
            <div className='card'>
                <div className='innercard'>
                    <div className='passinfo'>
                        <div className='image'>
                            <div 
                                className='communityimage' 
                                style={{backgroundImage:`url(${communityInfoParams.communityimage})`}}
                                onClick={communityDetail}
                            ></div>
                            <div className='userimage'>
                                <div className='innercircle'>
                                    <img src={userStore.myuserInfo.profilephoto} alt=''></img>
                                </div>
                            </div>
                        </div>
                        <div className='nftpasscard'>

                            <div className='nftpassimage'>
                                <img src={communityStore.mycommunitynftpass.nftimguri} alt=''></img>
                            </div>

                            <div className='btn' onClick={()=>{navigate(`/island/${communityInfoParams.communityname}`)}}>
                                 <Icon type="gorow"/>
                            </div>
                        </div>
 
                        
                            {communityStore.mymedal.list.length===0?
                            <>
                               <div className='moregorow'>
                                    
                                    <Icon type="gorow"/>
                                </div>
                                <div className='notes'>
                                    <br/>
                                    
                                </div>

                                <div className='moregorow'>
                                    
                                    <Icon type="gorow"/>
                                </div>
                                <div className='mymedal'>
                                <div className='item'>
                                    <div className='medal'>
                                        <div className='medalright'>
                                            <div className='inner'>
                                                
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                </div>
                            </>
                            :
                            <>
                                <div className='gorow'>
                                    <Icon type="gorow"/>
                                </div>
                        
                            <div className='scrolldiv'>
                            {communityStore.mymedal.list.map((item,index)=>(
                                     <div className='medalimage'>
                                    <NavLink
                                    className='polygon'
                                    to={`/task/${item.taskname}`}
                                    state={{
                                    communityname:communityInfoParams.communityname,
                                    communityimage:communityInfoParams.communityimage,
                                    communityid:communityid,
                                    taskimage:item.taskimage,
                                    deadline:item.deadline.substring(0,11),
                                    medalnumber:item.medalnumber,
                                    restmedal:item.restmedal,
                                    taskintro:item.taskintro,
                                    taskdetailrights:item.taskdetailrights,
                                    medalimguri:item.medalimguri,
                                    medalrights:item.medalrights,
                                    engname:communityInfoParams.engname,
                                    taskid:item.taskid,
                                    discord:item.discord
                                    }}
                                    >  

                               
                                <div className=''>
                                <img src={require("@/assets/Image/polygon.png")} alt=''></img>
                                </div>

                                <div className="medalpic">
                                <img src={item.medalimguri} alt=''></img>
                                </div>
                                {/* <div className="boxF">
                                <div className="boxS">
                                    <div className="boxT" style={{backgroundImage:`url(${item.medalimguri})`}}>
                                        <div className="overlay"></div>
                                    </div>
                                </div>
                                </div> */}
                             
                                </NavLink>  
                                   </div> 
                            ))}
                            </div>


                            <div className='gorow'>
                            <Icon type="gorow"/>
                        </div>

                        <div className='mymedal'>
                            <div className='item'>
                                    <div className='medal'>
                                        <div className='medalright'>
                                            <div className='inner'>
                                                
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            {communityStore.mymedal.list.map((item,index)=>(
                                <div className='item'>
                                    <div className='medal'>
                                        <div className='medalright'>
                                            <div className='inner'>
                                                {item.medalrights}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </div>
                            </>
                            
                        }
                            
                       
                        <div className='nftbtn' onClick={myholdnft}>
                            NFT 
                        </div>
                     
                        
                    </div>
                </div>
            </div>
        </div>
    )
}


export default observer(CommunityMember)