import styles from './taskdetail.module.scss'
import { observer } from 'mobx-react-lite'
import { useParams , useLocation, useNavigate  } from 'react-router-dom'
import { ImageViewer, NavBar, Popup, Toast } from 'antd-mobile'
import { getToken } from "@/utils/token"
import { useEffect, useState } from 'react'
import Icon from '@/components/Icon/icon'
import { useStore } from '@/store'


const TaskDetail = () => {

    const userphone=getToken().userphone
    const { taskname } = useParams()
    const taskInfoParams:any  = useLocation().state 
    const [visible, setVisible] = useState(false)
    const [redeemcode, setRedeemcode] = useState("");
    const { communityStore,userStore } = useStore()
    const [visible2, setVisible2] = useState(false)
    const [medalimg,setMedalimg] = useState("")
    const navigate = useNavigate()
    const [picturevisible, setPictureVisible] = useState(false)

    const [receivemedal,setReceiveMetal] = useState(taskInfoParams.medalnumber-taskInfoParams.restmedal)


    useEffect(() => {
      const runAction = async () => {
      await userStore.getUserAuthInfo(userphone!)
      }
      runAction()
    }, [userStore, userphone])


  
    async function ReceiveMedal() {

      const res = await communityStore.checkMedalCode(taskInfoParams.communityid,Number(taskInfoParams.taskid) ,userphone,redeemcode)

      if(res.data==="0"){
        Toast.show("")
      }
      else{
        setMedalimg(res.data)
        setVisible2(true)
      }
    }

  
    return (
      <div className={styles.root}>
        <NavBar className='navbar' onBack={()=>window.history.back()}></NavBar>
        <div className='communityinfo'>
            <div className='circle1'>
            <div className='circle2'>
                <img src={taskInfoParams.communityimage} alt='communityimg'></img>
            </div>
            </div>
            <div className='communityname'>
                <div className='name1'>{taskInfoParams.communityname}</div>
                <div className='name2'>{taskInfoParams.engname}</div>
            </div>

        </div>
        <div 
            className='bigtask'
            style={{backgroundImage:`url(${taskInfoParams.taskimage})`}} 
        >
            <div className='taskname'>
              <div className='name'>
                {taskname}
              </div>
            </div>

        </div>
 

        <div className='title'></div>
        <div className='remark'> : {taskInfoParams.deadline}</div>
        <div className='remark'> : {receivemedal}/{taskInfoParams.medalnumber}</div>

        <div className='content'>{taskInfoParams.taskintro}</div>

        <div className='title'></div>

        <div className='medalinfo'> 

          <div className='medalimage'>
            <div className='polygon'>
              <img src={require("@/assets/Image/polygon.png")} alt=''></img>
            </div>

            {/* <div className="boxF">
              <div className="boxS">
                <div className="boxT" style={{backgroundImage:`url(${taskInfoParams.medalimguri})`}}>
                    <div className="overlay"></div>
                </div>
              </div>
            </div> */}
             <div className="medalpic" onClick={() => {
          setPictureVisible(true)
        }}>
             <img src={taskInfoParams.medalimguri} alt=''></img>
           
             </div>

             <ImageViewer
              image={taskInfoParams.medalimguri}
              visible={picturevisible}
              onClose={() => {
                setPictureVisible(false)
              }}
            />

            
            </div> 

          <div className='medaldetail'>
            {taskInfoParams.taskdetailrights}
          </div>

            
            
        </div>

        <div className='btnrow'>
          <div className='rights' onClick={() => {
            // if(userStore.userAuthInfo.isid===0){
            //   Toast.show("")
            //   navigate(`/certification`)

            // }
            // else{
              setVisible(true)
            // }
            
            }} ></div>
            {
              taskInfoParams.discord==="null"?
              <div className='dao' onClick={()=>{ 
                Toast.show("")}}>DAO <Icon type={"communitycontactvx"}/></div>
              :
              <div className='dao' onClick={()=>{ window.location.href=`${taskInfoParams.discord}`}}>DAO <Icon type={"communitycontactvx"}/></div>

            }
          
          
        </div>
       

        <Popup
          className={styles.popup}
          visible={visible}
          onMaskClick={() => {
            setVisible(false)
          }}
        >
          <div className='card'>
            <div className='title'></div>
          
            <input 
              placeholder=''
              value={redeemcode}
              onChange={(e) => setRedeemcode(e.target.value) } 
              required={true}
            />

            <div className='detail'><strong>MetaIslands</strong></div>

            <button className='btn' onClick={ReceiveMedal}></button>
            <div className='closebtn' onClick={() => {setVisible(false);setRedeemcode('')}}><Icon type='closebtn'/></div>

          </div>

           
        </Popup>

        <Popup
          className={styles.medalpopup}
          visible={visible2}
          onMaskClick={() => {
            setVisible(false)
            window.location.reload()

          }}
        >
          <div className='card'>
            <img src={medalimg} alt="nftpass" />
            <div className='title'>
              {/* <Icon type="crown"/> */}
              
            </div>

            <button className='btn' 
            onClick={() => {
              setVisible2(false);
              window.location.reload();
              taskInfoParams.medalnumber=taskInfoParams.medalnumber-1;
              setReceiveMetal(receivemedal+1);
           
              }}></button>
        
            <div className='closebtn' onClick={() => {setVisible2(false);window.location.reload()}}><Icon type='closebtn'/></div>
          </div>
        </Popup>

      </div>
    )
}

export default observer(TaskDetail)
