import { observer } from 'mobx-react-lite'
import { useEffect, useState } from 'react'
import { useStore } from '@/store'
import styles from './nftserieslist.module.scss'
import { InfiniteScroll, NavBar, PullToRefresh, SearchBar } from 'antd-mobile'
import { useLocation, useParams } from 'react-router-dom'
import NftCard from '@/pages/NFTCard/NftCard/nftcard'
import SoldNftCard from '@/pages/NFTCard/SoldNftCard/soldnftcard'
import Icon from '@/components/Icon/icon'
import { NavLink } from 'react-router-dom'

const PAGE=1
const PAGESIZE=6

const initPage = {
 page:PAGE,          
 pageSize: PAGESIZE
}


const NftSeriesList = () => {

  const InfoParams:any  = useLocation().state 
  const { communityid,nftpassseries } = useParams()
  const { communityStore,searchStore } = useStore()
  const [data, setData] = useState<any>([])
  const [hasMore, setHasMore] = useState(true)
  const [pageParams, setPageParams] = useState(initPage)
  async function onsearch(value:any){
    if (!value) {
      searchStore.searchnft.results = []
    }
    else{
      const res = await searchStore.getSearchNft(value)
      //console.log("res",res)
    }
  }


  const loadMore = async () => {
      const res:any = await communityStore.getCommunityNftSeriesList(parseInt(nftpassseries!),parseInt(communityid!), pageParams.page, pageParams.pageSize )

      setData((val: any) => [...val, ...res.data.records])
      setPageParams({
      ...pageParams,
      page: parseInt(res.page) + 1
      })
      //results.length>0
      setHasMore(res.data.records.length >0)   
  }

  const onRefresh = async () => {
      setPageParams(initPage)
      const res:any= await communityStore.getCommunityNftSeriesList(parseInt(nftpassseries!),parseInt(communityid!), PAGE, PAGESIZE )
      setPageParams({
      ...pageParams,
      page: parseInt(res.page) + 1
      })
      setHasMore(res.data.records.length === pageParams.pageSize)
      setData(res.data.records)
  }


    return(
      <div className={styles.root}>
        <div className='bgcircle1'></div>
        <div className='bgcircle2'></div>
        <div className='bgcircle3'></div>
        <div className='bgcircle4'></div>
        <div className='bgcircle5'></div>
        <NavBar className="navbar" onBack ={()=>window.history.back() }>{InfoParams.nftseriesname}</NavBar>
        <div className='search'>
          <div className='searchbox'>
            <SearchBar
              placeholder="" 
              onSearch={val=>{onsearch(val)}}
              showCancelButton
            />
          </div>
         
          <div className='searchicon'>
            <div className='bluebg'>
            <Icon type="search"/>

            </div>
            
          </div>
        </div>
        <div className='list'>
            <PullToRefresh onRefresh={onRefresh}>
              {data.map((item:any) => { 
              if(item.issell===1){
                
                  return (
                    <div className="nftitem">
                       <NavLink
                        to={`/nft/${InfoParams.thecommunityname}/${item.nfttokenid}`}
                        state={{
                          nftimguri:item.nftimguri,
                          nftname:item.nftname,
                          createusername:item.username,
                          createprofilephoto:item.profilephoto,
                          nftprice:item.nftprice,
                          nftlikenumber:item.nftlikenumber,
                          communityid:item.communityid
                        }}> 

                        <div className='card'>
                          <div className='innercard'>
                            <div className='inner'>
                              <div
                                className="image"  
                                style={{backgroundImage:`url(${item.nftimguri})`}} 
                                >
                              </div>
                              <div className='nftname'>{item.nftname}</div>
                            </div>
                          </div>

                        </div>
                        </NavLink>
                      </div>
                  )
                  
                      }
                      else{

                        return (

                          <div className="nftitem">
                            
                             <NavLink
                              to={`/nft/${InfoParams.thecommunityname}/${item.nfttokenid}`}
                              state={{
                                nftimguri:item.nftimguri,
                                nftname:item.nftname,
                                createusername:item.username,
                                createprofilephoto:item.profilephoto,
                                nftprice:item.nftprice,
                                nftlikenumber:item.nftlikenumber,
                                communityid:item.communityid
                              }}> 
      
                              <div className='card'>
                                <div className='innercard'>
                                  <div className='inner'>
                                    <div
                                      className="image1"  
                                      style={{backgroundImage:`url(${item.nftimguri})`}} 
                                      >
                                    </div>
                                    <div className='nftname1'>{item.nftname}</div>
                                  </div>
                                </div>
      
                              </div>
                              </NavLink>
                        
                          
                            </div>
                      
                        )
                          }

                      
                
              
              })}
                
            </PullToRefresh>
            <InfiniteScroll loadMore={loadMore} hasMore={hasMore} /> 
          </div>
      
  
      
      </div>

    )
    
  

  
}

export default  observer(NftSeriesList)