import "@/assets/Icon/svgicon"

//  props 
type Props = {
    // icon 
    type: string | JSX.Element
    // 
    onClick?: () => void
  }
  
  const Icon = ({ type, onClick }: Props) => {
    return (
      <svg
        className="icon"
        aria-hidden="true"
        onClick={ onClick }
      >
        <use xlinkHref={`#${type}`}></use>
      </svg>
    )
  }
  
  export default Icon