// 
import { makeAutoObservable } from 'mobx'
import request from '@/utils/request'
import type { CommunityList, NftList} from '@/types/data'

class SearchStore {
  searchnft: NftList
  searchcommunity : CommunityList
  constructor() {
    makeAutoObservable(this)
    this.searchnft = { num:'',results:[] }
    this.searchcommunity ={num:'',list:[]} 
  }


  // NFT
  getSearchNft = async (KeyWord: string) => {
    //console.log("------------------",KeyWord)
    const res = await request.get('/searchNft', {
      params: {
        KeyWord
      }
    })
    this.searchnft.results = res.data.list
    //console.log("GET---------------------------")
    return res.data
  }


  // community
  getSearchCommunity = async (KeyWord: string) => {
    //console.log("------------------",KeyWord)
    const res = await request.get('/searchCommunity', {
      params: {
        KeyWord
      }
    })
    this.searchcommunity.list = res.data.list
    //console.log("GET---------------------------")
    return res.data
  }
  
}

export default SearchStore
