import styles from './certification.module.scss'
import { observer } from 'mobx-react-lite'
import { NavBar} from 'antd-mobile'
import { getToken } from '@/utils/token'
import { useState } from 'react'
import request from '@/utils/request'
import { useNavigate } from 'react-router-dom'
import { Toast,Form,Button,List,Input} from 'antd-mobile'
import { useStore } from '@/store'


const Certification = () => {

  const navigate  = useNavigate()
  //
  const back=()=>{
    window.history.back()
  }

  const [realName, setRealName] = useState("");
  const [idCardNumber, setIdCardNumber] = useState("");
  const { userStore } = useStore()

  const onFinish = async() => {
    await userStore.UserCertification(userphone!,realName,idCardNumber)
  
  }
  const userphone = getToken().userphone
  const phone =  userphone.replace(/^(\d{3})\d{4}(\d+)/, '$1****$2');


  return(
    <div className={styles.root}>
      <NavBar className="navbar" onBack={back}></NavBar>
      <div className='bgcircle1'></div>
      <div className='bgcircle2'></div>
      <div className='bgcircle3'></div>
      <div className='bgcircle4'></div>
      <div className='bgcircle5'></div>

      <Form className='form' onFinish={onFinish} >
        <div className='title'></div>
        <div className='notes'><br/></div>
        <div className='content'>
          
        </div>

        <div className='box'>
          {phone}
           
        </div>
             

        <div className='box'>
          <input  
            value={realName}
            placeholder=""
            onChange={(e) => setRealName(e.target.value)} />
        </div>
             
        <div className='box'>
          <input  
          value={idCardNumber}
          placeholder=""
          onChange={(e) => setIdCardNumber(e.target.value)} />
        </div>
            
       
        <Button
          block
          type="submit"
          color="primary"
          className="submit"
        >
          
        </Button>
          
      </Form>
    </div>
  )
    
}

export default Certification