import { useStore } from "@/store"
import styles from './myholdcommunitynft.module.scss'
import { useEffect } from "react"
import HoldNftCard from "@/pages/NFTCard/HoldNftCard/holdnftcard"
import { observer } from 'mobx-react-lite'
import { getToken } from "@/utils/token"
import { NavBar} from 'antd-mobile'
import MyNftList from "@/pages/NFTCard/MyNft/mynftlist"
import Notsellnftlist from "../../NFTCard/NotSellNFTList/notsellnftlist"
import { useLocation, useParams } from "react-router-dom"
import { NavLink } from "react-router-dom"

const MyHoldCommunityNft = () => {

  const { communityid } = useParams()
  const communityInfoParams:any  = useLocation().state 
  //
  const back=()=>{
    window.history.back() 
  }
  const userphone = getToken().userphone

  const { displayNftListStore } = useStore()
    
    useEffect(() => {
        // action
        const runAction = async () => {
        await displayNftListStore.getMyHoldCommunityNftList(userphone,communityid!)
        }
        runAction()
    }, [displayNftListStore,userphone,communityid])
 
 
    return(
        <div className={styles.root}>
          <NavBar className="navbar" onBack={back}></NavBar>
          <div className="list">
              {displayNftListStore.myholdcommunitynftlist.results.map((item) => {
                return (
                  <div className="nftitem">
                   <div className='card'>
                     <div className='innercard'>
                       <div className='inner'>
                         <div
                           className="image"  
                           style={{backgroundImage:`url(${item.nftimguri})`}} 
                           >
                         </div>
                         <div className='nftname'>{item.nftname}</div>
                       </div>
                     </div>

                   </div>
                 </div>
                )
          })}

          </div>           
        </div>
    )
}

export default  observer(MyHoldCommunityNft)