/**
 * nftcard
 * FindNftListnft list
 */

import { Image } from 'antd-mobile'
import styles from './nftcard.module.scss'
import type { NFTInfo } from '@/types/data'
import { useNavigate } from 'react-router-dom'
import Icon from '@/components/Icon/icon'
import { Link } from 'react-router-dom'
import { history } from '@/utils/history'
import { NavLink } from 'react-router-dom'
import { useState } from 'react'
import Nftdetail from '@/pages/NFTDetail/nftdetail'

type Props = {
  item: NFTInfo
}
 
const NftCard = ({ item}: Props) => {
  // const [data,setData] = useState({
  //   communityname:'',
  //   nfttokenid:''
  // })
  const navigator = useNavigate()

  const navigeUserDisplay = () => {
    //navigator(`/user/${item.username}`)
  }


 

  return (
    <div className={styles.root} >
       
      <NavLink 
        to={`/nft/${item.communityname}/${item.nfttokenid}`}
        state={{
          nftimguri:item.nftimguri,
          nftname:item.nftname,
          createusername:item.username,
          createprofilephoto:item.profilephoto,
          nftprice:item.nftprice,
          nftlikenumber:item.nftlikenumber,
          communityname:item.communityname
      }}>  
        <div
        className="image"  
        style={{backgroundImage:`url(${item.nftimguri})`}} 
        >
        </div>
      </NavLink>
        
      <div className='nftinfo' >

        <div className='row1'>
          <div className='nftname'>
            <div>{item.nftname}</div>
          </div>
          <div className='nftprice'>
            {item.cansell===0 ? '':` ${item.nftprice}`}
          </div> 
        </div>

        <div className='row2'>
          <div className='userinfo'>
            <div className="avatar" onClick={navigeUserDisplay}>
              <img
                src={item.profilephoto}
                alt=""
              />
            </div>
            <div className="username" onClick={navigeUserDisplay}>
              <div>{item.username}</div>
            </div>
          </div>


          <div className="nftlike">
            <Icon type="like_black"/>
            <div className='likenumber'>{item.nftlikenumber}</div>
          </div>

        </div>


      </div>
      
       
        
    </div>
  
  )
}
 

export default NftCard 
