// 
import { makeAutoObservable } from 'mobx'
import request  from '@/utils/request'
import type { CommunityList, NftList } from '@/types/data'


class NftListStore {
  communitynftlist : NftList
  selectnftlist:NftList
  selectnftpasslist:NftList
  selectcommunitylist:CommunityList
  constructor() {
    makeAutoObservable(this)
    this.selectnftlist = { num:'',results:[] }
    this.selectnftpasslist = { num:'',results:[] }
    this.selectcommunitylist = { num:'',list:[] }
    this.communitynftlist = { num:'',results:[] }
  }

    
  //NFT
  getNftList = async () => {
    const res = await request.get('/getNFTList')
    this.selectnftlist.results=[...res.data.result]                                                                                
    return res.data
  }

  //4NFT
  getCommunityNftList4 = async (communityid:number) => {
    const res = await request.get('/getCommunityNftList4',{
      params:{
        communityid
      }
    })
    //console.log("res.data=",res.data.data)
    
    //console.log("res.data.imguri=",res.data.result.map(Number))
    // this.communitynftlist.results=[...res.data.data]                                                                                
    //console.log("this.communitynftlist.list=",this.communitynftlist.results)
    return res.data.data
  }



  //16NFT
  getSelectNftList = async () => {
    const res = await request.get('/home/getSelectNftList')
    //console.log("res.data=",res.data.result)
    //console.log("res.data.imguri=",res.data.result.map(Number))
    this.selectnftlist.results=[...res.data.data]                                                                                
    //console.log("this.nftlist.list=",this.selectnftlist.results)
    //return res.data
  }
 

  //16NFT PASS  PASS
  getSelectNftPassList = async () => {
    const res = await request.get('/home/getSelectNftPassList')

    this.selectnftpasslist.results=[...res.data.data]                                                                                
    // return res.data
  }

  //16
  getSelectCommunityList = async () => {
    const res = await request.get('/home/getSelectCommunityList')
    this.selectcommunitylist.list=[...res.data.data]                                                                                
    //return res.data
  }

  
}

export default NftListStore