// 
import { makeAutoObservable } from 'mobx'
import request from '@/utils/request'
import type {BannerList, communityDiscord, CommunityItem, NFTInfo, NftList} from '@/types/data'



class BannerStore { 
  communityInfo : CommunityItem
  communitynft : NftList
  activeKey: Number
  communitydiscord:communityDiscord
  mycommunitynftpass:NFTInfo
  bannerlist:BannerList
  
  
  constructor() {
    makeAutoObservable(this)
    this.communityInfo = {} as CommunityItem
    this.communitynft = { num:'',results:[]}
    this.activeKey = 1
    this.communitydiscord ={qqfile:'',vxfile:''} as communityDiscord
    this.mycommunitynftpass={}as NFTInfo
    this.bannerlist = { num:'',bannerlist:[]}
  }

  getBannerList = async()=>{
    const res = await request.get('/getBannerList',{
        params:{ }
      })
      this.bannerlist.bannerlist = res.data.data
     
  }



  //PASS
  getMycommunityNftPASS = async (communityid:number,userphone:string)=>{

    const res = await request.get('/getMyCommunityNftPASS',{
      params:{
        communityid,
        userphone
      }
    })
    this.mycommunitynftpass = res.data.data

  }


}
export default BannerStore