// 
import { makeAutoObservable } from 'mobx'
import request from '@/utils/request'
import type {CommunityActionList, communityDiscord, CommunityItem, NFTInfo, NftList, TasksList, UserList} from '@/types/data'



class CommunityStore { 
  communityBriefInfo:CommunityItem
  communityInfo : CommunityItem
  communitynft : NftList
  activeKey: Number
  communitydiscord:communityDiscord
  mycommunitynftpass:NFTInfo
  memberList:UserList
  communityTasksList:TasksList
  finishTasksList:TasksList
  communityActionList:CommunityActionList
  mymedal:TasksList
  constructor() {
    makeAutoObservable(this)
    this.communityBriefInfo = {} as CommunityItem
    this.communityInfo = {} as CommunityItem
    this.communitynft = { num:'',results:[]}
    this.activeKey = 1
    this.communitydiscord ={qqfile:'',vxfile:''} as communityDiscord
    this.mycommunitynftpass={}as NFTInfo
    this.memberList = { list:[]} 
    this.communityTasksList = { list:[]} 
    this.finishTasksList = { list:[]} 
    this.communityActionList = { list:[]} 
    this.mymedal = { list:[]} 
  }

  // 
  getcommunityEnter= async (communityname:string) => {
    const res = await request.get('/getcommunityEnter',{
      params:{
          communityname
        }
    })
    // console.log("--------------------",res.data.data)
    this.communityBriefInfo = res.data.data


  }


  // 
    getcommunityDetail = async (communityname:string,userphone:string) => {
    const res = await request.get('/communityDetail',{
      params:{
          communityname,
          userphone,
        }
    })
    this.communityInfo=res.data.data
  }

  
  // nft
  getCommunityNftList = async (communityname:string,page:number,pageSize:number) => { 
    const res = await request.get('/getCommunityNftList', {
      params: {
        communityname,
        page,
        pageSize,
      }, 
    })
    return res.data
  }


    // NFT PASS
    getCommunityNftSeriesList = async (nftpassseries:number,communityid:number,page:number,pageSize:number) => { 
      const res = await request.get('/getCommunityNftSeriesList', {
        params: {
          communityid,
          nftpassseries,
          page,
          pageSize,
        },
      })
      return res.data
    }

  

 // key
 changeActiveKey = (id: Number) => {
  this.activeKey = id
}

  // 
  getcommunityDiscord = async (communityname:string) => {
    const res = await request.get('/communityDiscord',{
      params:{
          communityname,
        }
    })
    this.communitydiscord=res.data
  }

  //PASS
  getMycommunityNftPASS = async (communityid:number,userphone:string)=>{

    const res = await request.get('/getMyCommunityNftPASS',{
      params:{
        communityid,
        userphone
      }
    })
    this.mycommunitynftpass = res.data.data

  }


  //

  getMycommunityMedal = async (communityid:number,userphone:string)=>{

    const res = await request.get('/getMycommunityMedal',{
      params:{
        communityid,
        userphone
      }
    })
    
    this.mymedal.list = res.data.data

  }


  //
  getMemberList = async (communityid:number)=>{
    const res = await request.get('/getMemberList',{
      params:{
        communityid
      }
    })
    this.memberList.list = res.data.data
  }


  //
  getCommunityTasksList = async(communityid:number)=>{
    const res = await request.get('/getCommunityTasksList',{
      params:{
        communityid
      }
    })
    this.communityTasksList.list = res.data.data
  }


  //
  getFinishTasksList = async(communityid:number)=>{
    const res = await request.get('/getFinishTasksList',{
      params:{
        communityid
      }
    })
    this.finishTasksList.list = res.data.data
  }


  //
  getCommunityActionList =  async(communityid:number)=>{
    const res = await request.get('/getCommunityActionList',{
      params:{
        communityid
      }
    })
    this.communityActionList.list = res.data.data
  }

  // 
  checkMedalCode =  async(communityid:number,taskid:number,userphone:string,redeemcode:string)=>{
    const res = await request.get('/checkMedalCode',{
      params:{
        communityid,
        taskid,
        userphone,
        redeemcode
      }
    })
    return res.data
  }




}


export default CommunityStore