import styles from './communitydetail.module.scss'
import { observer } from 'mobx-react-lite'
import { useParams } from 'react-router-dom'
import { useStore } from '@/store'
import { useEffect, useState } from 'react'
import { getToken } from '@/utils/token'
import { Popup, NavBar,Tabs, Toast,FloatingBubble } from 'antd-mobile'
import NFTPASSSeries from './CommunityNFTSeries/nftseriesItem'
import Icon from '@/components/Icon/icon'
import Dao from './Dao/dao'
import { useNavigate } from 'react-router-dom'
import { NavLink } from 'react-router-dom'
import Communityaction from './CommunityAction/communityaction'


const CommunityDetail = () => {

   //
   const back=()=>{
    window.history.back()
  }

  const navigate = useNavigate()

  const { communityname } = useParams()
  const userphone=getToken().userphone
  const [visible, setVisible] = useState(false)
  const { communityStore } = useStore()
  const [nftimg,setNftimg] = useState("")

  useEffect(() => {
    const runAction = async () => {
    await communityStore.getcommunityDetail(communityname!,userphone!)
    }
    runAction()
  }, [communityStore, communityname,userphone])

  const { communityListStore } = useStore()



  useEffect(() => {
    // action
    const runAction = async () => {
    await communityListStore.getCommunityNftSeries(communityname!)
    }
    runAction()
}, [communityListStore,communityname])


  async function joinCommunity(){

    if(communityStore.communityInfo.communitymembers===communityStore.communityInfo.communityscale){

      Toast.show("PASS");
    }
    else{
      const res = await communityListStore.joinCommunity(communityname!,userphone)
      setNftimg(res.data)
      setVisible(true)
    }
  }
    

  // tab
  const typeChange = (key: string) => {
    // key
    communityStore.changeActiveKey(Number(key))
    // # bug
    //findnftStore.refreshList(Number(key))  
  }

 
  return (
    <div className={styles.root}>
      <div className='bgcircle1'></div>
      <div className='bgcircle2'></div>
      <div className='bgcircle3'></div>
      <div className='bgcircle4'></div>
      <div className='bgcircle5'></div>

      <div className='row1'> 
        <div className='navbar'>
          <NavBar onBack={back}></NavBar>
        </div>
      
        {/* <div className='btn'>  
          <Icon type="community_share"/>
          <Icon type="search"/>
        </div> */}

      </div>

      {/* <Mask visible={visible} onMaskClick={() => setVisible(false)}>
        <div className={styles.mask}>
          <img src={nftimg} alt="nftpass" />
        <div>PASS</div>
        </div>
      </Mask> */}
      
      <div className='header'>
        
       <div className='communityimg'>
        <div className='circle1'>
          <div className='circle2'>
            <img src={communityStore.communityInfo.communityimage} alt='communityimg'></img>
          </div>
        </div>

       </div>
       

        <div className='communityname'>
          {communityStore.communityInfo.communityname}
        </div>

        <div className='engname'>
          {communityStore.communityInfo.engname}

        </div>
        
        <div className='communityslogan'>
          {communityStore.communityInfo.communityslogan}
        </div>

        <div className='contact'>
          <div className="contacticon">
          {
            communityStore.communityInfo.communitycontactrb==="null"?
              <Icon  type="contactredbook"></Icon>
              :
              <a href={communityStore.communityInfo.communitycontactrb}>
                <Icon  type="contactredbook"></Icon>
              </a>
          }
          </div>

          <div className="contacticon">
          {
            communityStore.communityInfo.communitycontactdy==="null"?
            <Icon  type="jike"></Icon>
            :
            <a href={communityStore.communityInfo.communitycontactdy}>
              <Icon  type="jike"></Icon>
            </a>
          }
          </div>

          <div className="contacticon">
          {
          communityStore.communityInfo.communitycontactwb==="null"?
              <Icon  type="ems-cnpl"></Icon>
              :
              <a href={communityStore.communityInfo.communitycontactwb}>
                <Icon  type="ems-cnpl"></Icon>
              </a>
          }
          </div>
        </div>

        <div className='addbtn'>
        {
          communityStore.communityInfo.ismember === 1?
          <div>

          <NavLink
            to={`/mynftpass/${communityStore.communityInfo.communityid}`}
            state={{
              communityimage:communityStore.communityInfo.communityimage,
              communityname:communityname,
              engname:communityStore.communityInfo.engname
            }}>  
            <button></button>
          </NavLink>  
          {
            communityStore.communityInfo.communitycontactvx==="null"?
            <button onClick={()=>{ Toast.show("")}}>DAO</button>
            :
            <button onClick={()=>{ window.location.href=`${communityStore.communityInfo.communitycontactvx}`}}>DAO</button>
          }
            
          </div>
          
          :
          <div>
            <button onClick={()=>{Toast.show("")}}></button>
            <button onClick={joinCommunity}></button>
          </div>
                
        }
        </div>
        
      </div>


      <Tabs  
          className='tab'
          activeKey={communityStore.activeKey.toString()}
          onChange={(key) => typeChange(key)} 
          
        >
          <Tabs.Tab title='' key='1' >
            <div className='brandabout'>
              <div className='intro'>
                <div className='title'>{communityname}</div>
                <div className='line'></div>
                <div className='content'>{communityStore.communityInfo.communityintro}</div>
              </div>
            </div>
            {/* <div className='foot'>
              
            </div> */}
          </Tabs.Tab>
           

          <Tabs.Tab title='' key='2'>
              {
                communityListStore.nftseries.list.length?
                <div className={"nftseries"}>
                  {
                    communityListStore.nftseries.list.map((item,index)=>{
                      return(
                        <div className='list' >
                          <NFTPASSSeries
                            key={index}
                            item={item}
                            communityname={communityname!}
                          />
                        </div>
                      )
                    })
                  }
                </div>
                :
                <div className='nulllist'>
                  <div className='image'> <Icon type="nonenft"/></div>
                  <div className='title'></div>
                </div>
              }
              
           
              <div className='threeD'  onClick={() => {
                if(communityStore.communityInfo.threeDcommunity==="null"){
                  Toast.show("3D")
                }
                else{
                  window.location.href=`${communityStore.communityInfo.threeDcommunity}`
                }
              }}>
                <Icon type="3D_btn" ></Icon>
              </div>
          </Tabs.Tab>


          <Tabs.Tab title='' key='3'>
            <Dao
              communityname={communityname!} 
              ismember={communityStore.communityInfo.ismember}
              communityid={communityStore.communityInfo.communityid}
              communityimage={communityStore.communityInfo.communityimage}
              engname={communityStore.communityInfo.engname}
              communitycontactvx={communityStore.communityInfo.communitycontactvx}
           
            ></Dao>
          </Tabs.Tab>
          
          <Tabs.Tab title='' key='4'>
            <Communityaction communityid={communityStore.communityInfo.communityid!} ismember={communityStore.communityInfo.ismember}></Communityaction>
          </Tabs.Tab>


      </Tabs>


      <Popup
          className={styles.popup}
          visible={visible}
          onMaskClick={() => {
            setVisible(false)
            window.location.reload()

          }}
        >
          <div className='card'>
            <img src={nftimg} alt="nftpass" />
            <div className='title'>
              <Icon type="crown"/>
              {communityStore.communityInfo.communitymembers+1}
            </div>

            <button className='btn' onClick={() => {setVisible(false);window.location.reload()}}></button>
        
            <div className='closebtn' onClick={() => {setVisible(false);window.location.reload()}}><Icon type='closebtn'/></div>
          </div>
        </Popup>

    
      
    </div>
  )
}

export default observer(CommunityDetail)
        

