
import { observer } from 'mobx-react-lite'
import { useStore } from '@/store'
import { useEffect } from 'react'
import { useNavigate, useParams} from 'react-router-dom'
import styles from './communityenter.module.scss'


const CommunityEnter = () => {

  const { communityname } = useParams()
  const { communityStore } = useStore()

  useEffect(() =>{
    // action
    const runAction = async () => {
      await communityStore.getcommunityEnter(communityname!)
    }
    runAction()
  }, [communityStore,communityname])

  const navigate = useNavigate()
  const navigeCommunityDetail = () => {
   navigate(`/island/${communityname}`)
 }

//  console.log("+++++++++++++++++ ",communityStore.communityBriefInfo.enterpost)

  return (
 
    <div className={styles.root} style={{backgroundImage:`url(${communityStore.communityBriefInfo.enterpost})`}} >
      <div className='bg'></div> 
      <div className="loginform">
        <div className='title'>
         {communityname}</div>
         <div className='slogan'>
          {communityStore.communityBriefInfo.communityslogan}

         </div>
          <div className='btn' onClick={navigeCommunityDetail}>
            
          
        </div>
          
      
      </div>
    </div>
  )
}
export default observer(CommunityEnter)