import { useEffect, useState } from 'react'
import { PullToRefresh, InfiniteScroll} from 'antd-mobile'
import { Swiper } from 'antd-mobile'
import { useStore } from '@/store'
import styles from './findcommunitylist.module.scss'
import Icon from '@/components/Icon/icon'
import { useNavigate } from 'react-router-dom'
import BrandCard from '../NFTCard/BrandCard/brandcard'

 const PAGE=1
 const PAGESIZE=2


const initPage = {
  page:PAGE,          
  pageSize: PAGESIZE
}

// eslint-disable-next-line import/no-anonymous-default-export
export default () => {

  const navigate = useNavigate()
  const { communityListStore,bannerStore } = useStore()

  useEffect(() =>{
    // action
    const runAction = async () => {
      await bannerStore.getBannerList()
    }
    runAction()
  }, [bannerStore])


  useEffect(() =>{
    // action
    const runAction = async () => {
      await communityListStore.getHotCommunityList()
    }
    runAction()
  }, [communityListStore])



  const [data, setData] = useState<any>([])
  const [hasMore, setHasMore] = useState(true)
  const [pageParams, setPageParams] = useState(initPage)
  const loadMore = async () => {
    const res:any = await communityListStore.getCommunityList(pageParams.page, pageParams.pageSize )
    setData((val: any) => [...val, ...res.data.records])
    setPageParams({
      ...pageParams,
      page: parseInt(res.page) + 1
    })
    setHasMore(res.data.records.length >0)   
  }

  const onRefresh = async () => {
    setPageParams(initPage)
    const res:any= await communityListStore.getCommunityList(PAGE, PAGESIZE)
    
    setPageParams({
      ...pageParams,
      page: parseInt(res.page) + 1
    })
    setHasMore(res.data.records.length === pageParams.pageSize)
    setData(res.data.records)
  }


    return(
      <div className={styles.root}> 
        <div className='header'>
          <div className='smalltitle'>
            <div className='name'>Pass</div>
           
          </div>
         
          <div className="search">
            <Icon type="search" />
          </div>
          
        </div>
          <div className='title'>
            <div className='name'></div>
           
          </div>


        {/* banner */}

        <Swiper loop className="swiper" autoplay={true}>
          {bannerStore.bannerlist.bannerlist.map((item,index) => (
            <Swiper.Item key={index}>
              <div 
                className="swiperitem"
                style={{backgroundImage:`url(${item.bannerimage})`}}
                onClick={() => {
                  window.location.href=`${item.jumpuri}`
                }}
              >
              </div>
            </Swiper.Item>
          ))}
        </Swiper> 


        {/*  */}
        <div className='hotbanner'></div>
        <div className='scrolldiv'>
          {communityListStore.hotcommunitylist.list.map((item,index)=>(
            <div className='circle1'  onClick={()=>{navigate(`/${item.communityname}`)}}>
              <div className='circle2'>
                <img src={item.communityimage} alt=''></img>
              </div>
            </div>
          ))}
        </div>
       
  

        {/*  */}
        <PullToRefresh onRefresh={onRefresh}>
          {data.map((item:any) => (
            <div className="nft-item">
              <BrandCard
                item={item}
                key={item.communityid}
              />
            </div>
          ))}
        </PullToRefresh>
        <InfiniteScroll loadMore={loadMore} hasMore={hasMore} />
      </div>

    )
   
  

}
 
