import UserStore from "./user.Store"
import React from "react"
import { configure } from "mobx"
import LoginStore from "./login.Store"
import NftListStore from "./nftList.Store"
import NFTInfoStore from "./nftinfo.Store"
import DisplayNftListStore from "./displaynftlist.Store"
import FollowListStore from "./followlist.Store"
import CommunityListStore from "./communityList.Store"
import CommunityStore from "./community.Store"
import SearchStore from "./search.Store"
import BannerStore from "./banner.Store"


configure({
  enforceActions: "never",
})

class RootStore {
  loginStore : LoginStore
  userStore : UserStore
  nftListStore : NftListStore
  displayNftListStore : DisplayNftListStore
  nftInfoStore : NFTInfoStore
  followListStore : FollowListStore
  communityListStore : CommunityListStore
  communityStore : CommunityStore
  searchStore : SearchStore
  bannerStore:BannerStore


  // 
  constructor() {
    this.loginStore = new LoginStore()
    this.userStore = new UserStore()
    this.nftListStore = new NftListStore()
    this.nftInfoStore = new NFTInfoStore()
    this.displayNftListStore = new DisplayNftListStore()
    this.followListStore = new FollowListStore()
    this.communityListStore = new CommunityListStore()
    this.communityStore = new CommunityStore()
    this.searchStore = new SearchStore()
    this.bannerStore =  new BannerStore()


  }
}
// storecontext
const StoresContext = React.createContext(new RootStore())
//  contex
export const useStore = () => React.useContext(StoresContext)