
import Icon from '@/components/Icon/icon'
import { useStore } from '@/store'
import { getToken } from '@/utils/token'
import { Toast } from 'antd-mobile'
import { observer } from 'mobx-react-lite'
import { useEffect } from 'react'
import { NavLink } from 'react-router-dom'
import { Navigate, useNavigate } from 'react-router-dom'
import styles from './dao.module.scss'

type Props={
  ismember:number
  communityname:string
  communityid:number
  communityimage:string
  engname:string
  communitycontactvx:string
 }

const Dao = ({ismember,communityname,communityid,communityimage,engname,communitycontactvx}:Props) => {

  const { communityStore } = useStore()

 
  useEffect(() => {
    const runAction = async () => {
     await communityStore.getMemberList(communityid!)
    }
    runAction()
  }, [communityStore, communityid])

  useEffect(() => {
    const runAction = async () => {
     await communityStore.getFinishTasksList(communityid!)
    }
    runAction()
  }, [communityStore, communityid])

  useEffect(() => {
    const runAction = async () => {
     await communityStore.getCommunityTasksList(communityid!)
    }
    runAction()
  }, [communityStore, communityid])



  if(ismember===0){
    return(
      <div className={styles.root} onClick={()=>{Toast.show("")}}>
      {communityStore.memberList.list.length===0?
      <></>
      :
      <>
       <div className='title'></div>

       <div className='scrolldiv'>
         {communityStore.memberList.list.map((item,index)=>(
           <div className='circle1'>
             <div className='circle2'>
               <img src={item.profilephoto} alt=''></img>
             </div>
           </div>
         ))}
       </div>
     </>
     }
     

     <div className='title'></div>
    
     <div className='taskscrolldiv'>
         {communityStore.finishTasksList.list.map((item,index)=>(
    
           <div 
             className='smalltask'
             style={{backgroundImage:`url(${item.taskimage})`}} 
           >
             <div className='taskname'>
               <div className='name'>
                 {item.taskname}
               </div>
             </div>

           </div>
      
         ))}
    </div> 
   
   


   {communityStore.communityTasksList.list.length===0?
       <div className='nulllist'>
         <div className='image'> <Icon type="nonenft"/></div>
         <div className='title'></div>
       </div>
     :
    <>
     {communityStore.communityTasksList.list.map((item,index)=>(
     
    
   
       <div 
         className='bigtask'
         style={{backgroundImage:`url(${item.taskimage})`}} 
       >
         <div className='taskname'>
           <div className='name'>
             {item.taskname}
           </div>
         </div>

         <div className='infobg'>

         <div className='taskinfo'>
          <div className='row'></div>
          <div className='row'>:{item.deadline.substring(0,11)}</div>
          <div className='row'>:{item.medalnumber-item.restmedal}/{item.medalnumber}</div>
         </div>

         <div className='dao'>
           <div className='circle1'>
             <div className='circle2'>
               <div className='discord'>
                 <div>DS<br/>DAO<br/>
                 {communitycontactvx==="null"?
                 <Icon type="communitycontactvx"/>
                 :
                 <a href={communitycontactvx}><Icon type="communitycontactvx"/></a>
                 
                }
                   
                 </div>
               </div>
             </div>
           </div>

         </div>
       </div>
       </div>
     
       ))}
     </>
   }
       
 
   </div>
    )
}

  

//
  else{
    return(
      <div className={styles.root}>
         {communityStore.memberList.list.length===0?
         <></>
         :
         <>
          <div className='title'></div>
  
          <div className='scrolldiv'>
            {communityStore.memberList.list.map((item,index)=>(
              <div className='circle1'>
                <div className='circle2'>
                  <img src={item.profilephoto} alt=''></img>
                </div>
              </div>
            ))}
          </div>
        </>
        }
        

        <div className='title'></div>
       
        <div className='taskscrolldiv'>
            {communityStore.finishTasksList.list.map((item,index)=>(
              <NavLink
                // className='smalltask'
                to={`/task/${item.taskname}`}
                state={{
                  communityname:communityname,
                  communityimage:communityimage,
                  communityid:communityid,
                  taskimage:item.taskimage,
                  deadline:item.deadline.substring(0,11),
                  medalnumber:item.medalnumber,
                  restmedal:item.restmedal,
                  taskintro:item.taskintro,
                  taskdetailrights:item.taskdetailrights,
                  medalimguri:item.medalimguri,
                  medalrights:item.medalrights,
                  engname:engname,
                  taskid:item.taskid
                }}
              >  
              <div 
                className='smalltask'
                style={{backgroundImage:`url(${item.taskimage})`}} 
              >
                <div className='taskname'>
                  <div className='name'>
                    {item.taskname}
                  </div>
                </div>

              </div>
              </NavLink>  
            ))}
      </div>
      
      


      {communityStore.communityTasksList.list.length===0?
          <div className='nulllist'>
            <div className='image'> <Icon type="nonenft"/></div>
            <div className='title'></div>
          </div>
        :
       <>
        {communityStore.communityTasksList.list.map((item,index)=>(
        
        <NavLink
            className='bigtask'
            to={`/task/${item.taskname}`}
            state={{
              communityname:communityname,
              communityimage:communityimage,
              communityid:communityid,
              taskimage:item.taskimage,
              deadline:item.deadline.substring(0,11),
              medalnumber:item.medalnumber,
              restmedal:item.restmedal,
              taskintro:item.taskintro,
              taskdetailrights:item.taskdetailrights,
              medalimguri:item.medalimguri,
              medalrights:item.medalrights,
              engname:engname,
              taskid:item.taskid,
              discord:item.discord
            }}
          >  
      
          <div 
            className='bigtask'
            style={{backgroundImage:`url(${item.taskimage})`}} 
          >
            <div className='taskname'>
              <div className='name'>
                {item.taskname}
              </div>
            </div>

            <div className='infobg'>

              <div className='taskinfo'>
                <div className='row'></div>
                <div className='row'>:{item.deadline.substring(0,11)}</div>
                <div className='row'>:{item.medalnumber-item.restmedal}/{item.medalnumber}</div>
              </div>

              <div className='dao'>
                <div className='circle1'>
                  <div className='circle2'>
                  {
                  item.discord==="null"?
                  <div className='discord' onClick={()=>{ Toast.show("")}}>
                      <div>DS<br/>DAO<br/>
                        <Icon type="communitycontactvx"/>
                      </div>
                    </div>
                    :

                    <div className='discord' onClick={()=>{ window.location.href=`${item.discord}`}}>
                      <div>DS<br/>DAO<br/>
                        <Icon type="communitycontactvx"/>
                      </div>
                    </div>

                  }
                  </div>
                </div>
              </div>
              
            </div>
            

          </div>
          </NavLink>  
          ))}
        </>
      }
          
    
      </div>
    )

  }
  
  
}

export default  observer(Dao)