import classnames from 'classnames'
import { NavBar, SearchBar} from 'antd-mobile'
import styles from './searchnft.module.scss'
import { useStore } from '@/store'
import { observer } from 'mobx-react-lite'
import {CloseOutline} from 'antd-mobile-icons'
import UserDisplayNft from '@/pages/NFTCard/HoldNftCard/holdnftcard'
import NftCard from '../NFTCard/NftCard/nftcard'
import CommunityNftCard from '../NFTCard/CommunityNftCard/communitynftcard'

const SearchNft = () => {
 
  const { searchStore } = useStore()
  async function onsearch(value:any){

    if (!value) {
      searchStore.searchnft.results = []
    }
    else{
      const res = await searchStore.getSearchNft(value)
      //console.log("res",res)
    }
  }

  const back=()=>{
    searchStore.searchnft.results = []
    window.history.back()
  }
  
  return (
    <div className={styles.root}>
      <NavBar
        className="navbar"
        onBack={back}
        backArrow={<CloseOutline />}
      > 
     
      <SearchBar 
        placeholder="" 
        onSearch={val=>{onsearch(val)}}
        showCancelButton
      />
      </NavBar>
      

      <div className={classnames('search-result', true ? 'show' : '')}>
        {
          searchStore.searchnft.results.map((item: any) => {
            if(item.communityid===0){
              return(
                <div className="result-item" key={item}>
                  <NftCard
                    item={item}
                  />
                </div>
              )
            }
            else{
              return(
                <div className="result-item" key={item}>
                  <CommunityNftCard
                    item={item}
                  />
                </div>
              )

            }
           
          })
        }
      </div>
              
    </div>
  )
}

export default observer(SearchNft)
