// 
import { makeAutoObservable } from 'mobx'
import request  from '@/utils/request'
import type { FollowList } from '@/types/data'


class FollowListStore {
 
  followinglist:FollowList
  followerslist:FollowList

  constructor() {
    makeAutoObservable(this)
    this.followinglist={list:[]}
    this.followerslist={list:[]}
  } 


  //
  
  //,insert
  insertFollow = async(userphone_fan:string,userphone_befollowed: string)=>{
    await request.get(`/insertFollow`,{
          params:{userphone_fan,userphone_befollowed}
    })
  }


  
    //,delete
    deleteFollow = async(userphone_fan:string,userphone_befollowed: string)=>{
      await request.get(`/deleteFollow`,{
          params:{userphone_fan,userphone_befollowed}
      })
  }

  //following
   getFollowingList = async(username:string) => {
    const res = await request.get('/getFollowingList',{
      params:{username}
    })
    this.followinglist.list=[...res.data.result]
    //console.log("res.data",res.data.result)
    //console.log("this.postnftlist.list=",this.followinglist.list)
    return res.data
  }


  //followers
  getFollowersList = async(username:string) => {
    const res = await request.get('/getFollowersList',{
      params:{username}
    })
    this.followerslist.list=[...res.data.result]
    //console.log("res.data",res.data.result)
    //console.log("this.postnftlist.list=",this.followinglist.list)
    return res.data
  }

}

export default FollowListStore