/**
 * nftcard
 * FindNftListnft list
 */


 import styles from './brandcard.module.scss'
 import type { CommunityItem } from '@/types/data'
 import { useNavigate } from 'react-router-dom'
 
 type Props = {
   item: CommunityItem
 }
 
 const BrandCard = ({ item}: Props) => {
  const navigator = useNavigate()
  
  const navigeCommunityDetail = () => {
    navigator(`/${item.communityname}`)
  }
  
   return (
    <div className={styles.root} onClick={navigeCommunityDetail}>
      <div className='bg'></div>
      <div className="image" style={{backgroundImage:`url(${item.communitybg})`}} ></div> 
      <div className='nftinfo'>
        <div className='nftname'>{item.communityname}</div>
      </div>
    </div>
   
   )
 }
  
 
 export default BrandCard 
 