// 
import { makeAutoObservable } from 'mobx'
import request  from '@/utils/request'
import type { CommunityList,NFTPASSSeriesList } from '@/types/data'


class CommunityListStore {
  mycommunitylist : CommunityList
  hotcommunitylist : CommunityList
  nftseries:NFTPASSSeriesList
  
  constructor() {
    makeAutoObservable(this)
    this.mycommunitylist = { num:'',list:[] }
    this.hotcommunitylist = { num:'',list:[] }
    this.nftseries={list:[]}
  }

    
  //community
  getMyCommunityList = async (userphone:string) => {
      const res = await request.get('/getMyCommunityList',{
          params:{
              userphone
          },
  
      })
      this.mycommunitylist.list=[...res.data.data]                                                                                
      //console.log("this.nftlist.list=",this.mycommunitylist.list)
      return res.data
  }

  

    //
    getMyMembers = async (userphone:string) => {
      const res = await request.get('/getMyMembers',{
          params:{
              userphone
          },
  
      })
      this.mycommunitylist.list=[...res.data.data]                                                                                
      return res.data
  }

    //community
    getCommunityList = async (page:number,pageSize:number) => {
      const res = await request.get('/getCommunityList', {
        params: {
          page,
          pageSize,
        },
      })

      return res.data
  }

   //rcommunity
   getHotCommunityList = async () => {
    const res = await request.get('/getHotCommunityList', {
      params: {
      },
    })

    this.hotcommunitylist.list=[...res.data.data]
}

   //nft 
   getCommunityNftSeries = async(communityname:string) => {
    const res = await request.get('/getCommunityNftSeries', {
      params: {
        communityname
      },
    })

    this.nftseries.list=[...res.data.data]
    

    //return res.data
  }


  //

  joinCommunity = async(communityname:string , userphone:string)=>{
    const res = await request.get('/joinCommunity',{
      params:{
        communityname,
        userphone
      },
    })
    return res.data

  }



}




export default CommunityListStore