// 
import { makeAutoObservable } from 'mobx'
import request  from '@/utils/request'
import type { CommunityList, NftList } from '@/types/data'


class DisplayNftListStore {
 
  mypostnftlist : NftList
  myholdnftlist : NftList
  mycreatenftlist : NftList
  hiscommunitylist:CommunityList
  holdnftlist:NftList
  myholdcommunitynftlist:NftList

  constructor() {
    
    makeAutoObservable(this)
    this.holdnftlist = { num:'',results :[] }
    this.mypostnftlist = { num:'',results :[] }
    this.myholdnftlist = { num:'',results:[] }
    this.mycreatenftlist = { num:'',results:[] }
    this.hiscommunitylist = { num:'',list:[] }
    this.myholdcommunitynftlist = { num:'',results:[] }
  }

    //NFT
    getMyPostNftList = async(userphone:string) => {
      const res = await request.get('/getMyPostNftList',{
        params:{userphone}
      })
      this.mypostnftlist.results=[...res.data.data]
      //return res.data
    }
 
   //NFT
   getMyHoldNftList = async(userphone:string) => {
    const res = await request.get('/getMyHoldNftList',{
      params:{userphone}
    })
    this.myholdnftlist.results=[...res.data.data]
    return res.data
  }


  //NFT
  getMyHoldCommunityNftList = async(userphone:string,communityid:string) => {
    const res = await request.get('/getMyHoldCommunityNftList',{
      params:{
        userphone,
        communityid
      }
    })
    this.myholdcommunitynftlist.results=[...res.data.data]
    return res.data
  }

     //NFT
     getMyCreateNftList = async(userphone:string) => {
      const res = await request.get('/getMyCreateNftList',{
        params:{userphone}
      })
      this.mycreatenftlist.results=[...res.data.data]
      return res.data
    }

    
  //
  getHisCommunityList = async (username:string) => {
    const res = await request.get('/getHisCommunityList',{
    params:{username}
  })
    this.hiscommunitylist.list=[...res.data.data]  
  
  }

    //NFT
    getHoldNftList = async(username:string) => {
      const res = await request.get('/getHoldNftList',{
        params:{username}
      })
      this.holdnftlist.results=[...res.data.data]
      //return res.data
    }

}

export default DisplayNftListStore