// 
import { makeAutoObservable } from 'mobx'
import request  from '@/utils/request'
import type {UserDisplayInfo,UserInfo, IslandWalletInfo_NftTransfer, IslandWalletInfo_Balance,NftTransferItem} from '@/types/data'

class userStore { 
  myIslandWallet_NftTransfer : IslandWalletInfo_NftTransfer
  myIslandWallet_CreateNftTransfer:IslandWalletInfo_NftTransfer
  myIslandWallet_Balance : IslandWalletInfo_Balance
  userAuthInfo : UserInfo
  myuserInfo: UserDisplayInfo  // 
  userDisplayInfo: UserDisplayInfo
  
 
  transactionInfo:NftTransferItem
  constructor() {
    makeAutoObservable(this)
    this.myuserInfo = {} as UserDisplayInfo
    this.userDisplayInfo = {} as UserDisplayInfo

    this.userAuthInfo = {} as  UserInfo
    this.myIslandWallet_NftTransfer= { islandwallet:0.0,results:[] }
    this.myIslandWallet_CreateNftTransfer= { islandwallet:0.0,results:[] }
    this.myIslandWallet_Balance = {islandwallet:0.0} 
    this.transactionInfo = {} as NftTransferItem
  }

  ///
  getUserAuthInfo = async(userphone:string) =>{ 
    const res = await request.get('/userInfo/userAuthInfo',{
      params:{userphone}
    })
    this.userAuthInfo.isid=res.data.data
  }




  UserCertification  = async(userphone:string,realname:string,idcardnumber:string) =>{ 
    const res = await request.get('/userInfo/userCertification',{
      params:{ userphone,
        realname,
        idcardnumber}
    })
    return res.data
  }


    //island 
    getIslandWallet_Balance =async(userphone:string) =>{
      const res = await request.get('/getIslandWallet_Balance',{
        params:{userphone}
      })
      //console.log("111111111111",res.data.data)
      this.myIslandWallet_Balance=res.data.data
    }
  
  //island nft
  getIslandWallet_NftTransfer =async(userphone:string) =>{
    const res = await request.get('/getIslandWallet_NftTransfer',{
      params:{userphone}
    })
 
    this.myIslandWallet_NftTransfer=res.data
  }

 //island nft
 getIslandWallet_CreateNftTransfer =async(userphone:string) =>{
  const res = await request.get('/getIslandWallet_CreateNftTransfer',{
    params:{userphone}
  })
  this.myIslandWallet_CreateNftTransfer=res.data
}


  // 
    getMyUserDisplay = async (userphone:string) => {
    const res = await request.get('/userInfo/getMyInfo',{
      params:{userphone}
    })
    this.myuserInfo=res.data.data
  }
  

  // 
  getUserDisplay = async (displayusername:string,userphone:string) => {
    const res = await request.get('/userInfo/getUserInfo',{
      params:{
        displayusername,
        userphone
      }
    })
    this.userDisplayInfo=res.data.data
  }



  // 
  updateUserName = async(userphone:string,username:string)=>{
    await request.post('/editinput',
    {
      userphone,username
    }
      
    )
  }


  //
  getTransaction1Detail = async(transfernft_id:string,userphone:string)=>{
    const res = await request.get('/getTransaction1Detail',{
    params:{
      transfernft_id,
      userphone
    }
    })
    this.transactionInfo=res.data
  }


  
  //
  getTransaction2Detail = async(transfernft_id:string)=>{
    const res = await request.get('/getTransaction2Detail',{
    params:{
      transfernft_id,
    }
    })
    this.transactionInfo=res.data
  }

}

export default userStore