import Icon from '@/components/Icon/icon'
import { useStore } from '@/store'
import { Toast } from 'antd-mobile'
import { observer } from 'mobx-react-lite'
import { useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import styles from './communityaction.module.scss'
type Props={
  ismember:number
  communityid:number
 }



const CommunityAction = ({ismember,communityid}:Props) => {
  const { communityStore } = useStore()
  const navigate = useNavigate()


  useEffect(() => {
    const runAction = async () => {
   //await communityStore.getcommunityDiscord(communityname!)
   await communityStore.getCommunityActionList(communityid!)
    }
    runAction()
  }, [communityStore, communityid])

  if(ismember===0){
    return(
      <div className={styles.root} onClick={()=>Toast.show("")}>
         {
          communityStore.communityActionList.list.length===0?
          <div className='nulllist'>
          <div className='image'> <Icon type="nonenft"/></div>
          <div className='title'></div>
        </div>
      :
      <>
      {communityStore.communityActionList.list.map((item,index)=>(
       <div 
         className='actionimage'
         style={{backgroundImage:`url(${item.actionimage})`}} 
       >
         <div className='actionname'>
           <div className='name'>
             {item.actionname}
           </div>
         </div>
       </div>
      ))}
      </>
  }


   </div>
    )
  }
  else{
    return(
      <div className={styles.root}>
         {
          communityStore.communityActionList.list.length===0?
          <div className='nulllist'>
          <div className='image'> <Icon type="nonenft"/></div>
          <div className='title'></div>
        </div>
      :
      <>
      {communityStore.communityActionList.list.map((item,index)=>(
          <div 
            className='actionimage'
            style={{backgroundImage:`url(${item.actionimage})`}} 
          >
            <div className='actionname'>
              <div className='name'>
                {item.actionname}
              </div>
            </div>

          </div>

         ))}
      </>
         }
         
        
      </div>
    ) 
  
  }
}

export default  observer(CommunityAction)