/**
 * nftcard
 * FindNftListnft list
 */


import styles from './nftseriesitem.module.scss'
import type { NFTPASSSeries as NFTPASSSeries_Item } from '@/types/data'
import { useNavigate } from 'react-router-dom'
import Icon from '@/components/Icon/icon'
import { NavLink } from 'react-router-dom'
 
type Props = {
  item: NFTPASSSeries_Item
  communityname:string

}
 
 const NFTPASSSeries = ({ item,communityname}: Props) => {

  return (
  <div className={item.sellnftpasscount===0? styles.root1:styles.root2} >
  
    <NavLink to={`/${item.communityid}/${item.nftpassseries}`}
      state={
        {
          thecommunityname:communityname,
          nftseriesname:item.nftseriesname
        }
      }
      
    >
      <div 
        className="image"  
        style={{backgroundImage:`url(${item.nftimguri})`}}
      >
      </div>
    </NavLink>

    <NavLink to={`/${item.communityid}/${item.nftpassseries}`}
      state={
        {
          thecommunityname:communityname,
          nftseriesname:item.nftseriesname
        }
      }
      
    >
     
      <div className='nftinfo' >
        <div className='title'>
          {item.nftseriesname}
        </div>
        <div className='intro'>
          {item.nftseriesintro}
        </div >
        <div className='nftnumber'>
        {item.nftnumber-item.sellnftpasscount}/{item.nftnumber}
        </div>
      </div>
      </NavLink>
  </div>
  )
}
  

 export default NFTPASSSeries 
 