import classnames from 'classnames'
import { NavBar, SearchBar, Toast } from 'antd-mobile'
import styles from './searchcommunity.module.scss'
import { useStore } from '@/store'
import { observer } from 'mobx-react-lite'
import {CloseOutline} from 'antd-mobile-icons'


const SearchCommunity = () => {

  const { searchStore } = useStore()
  async function onsearch(value:any){
    // Toast.show(`111111111111111111111:${value}`)
    if (!value) {
      searchStore.searchcommunity.list = []
    }
    else{
      const res = await searchStore.getSearchCommunity(value)
      //console.log("res",res)
    }
  }

  const back=()=>{
    searchStore.searchcommunity.list = []
    window.history.back()
  }
  
  return (
    <div className={styles.root}>
      <NavBar
        className="navbar"
        onBack={back}
        backArrow={<CloseOutline />}
      > 
     
         <SearchBar 
        placeholder="" 
        onSearch={val=>{onsearch(val)}}
        showCancelButton
      />
      
      </NavBar>
      

      <div className={classnames('search-result', true ? 'show' : '')}>
        {
          searchStore.searchcommunity.list.map((item: any) => {
            return(
              <div className="result-item" key={item}>
                {/* <FindCommunityNftList
                    item={item}
                /> */}
              </div>
            )
          })
        }
      </div>
              
    </div>
  )
}

export default observer(SearchCommunity)
